/* Special Thanks to joshmoto & sbgib*/
.Slide-text-container {
align-content: center;
width: 300px;
margin:5px;
}
/*Vertical Sliding*/
.sliding-Vertical { display: inline; text-indent: 14px; }
.sliding-Vertical span {
    animation: translate 12.5s linear infinite 0s;
    color: #00abe9;
    opacity: 0;
    overflow: hidden;
    position: absolute;
}
    .sliding-Vertical span:nth-child(2) { animation-delay: 2.5s; }
    .sliding-Vertical span:nth-child(3) { animation-delay: 5.0s; }
    .sliding-Vertical span:nth-child(4) { animation-delay: 7.5s; }
    .sliding-Vertical span:nth-child(5) { animation-delay: 10.0s; }

/*topToBottom Animation*/
@keyframes translate {
    0% { opacity: 0; }
    5% { opacity: 0;  transform: translate3d(35px, -35px, 0) }
    10% { opacity: 1; transform: translateY(0px); }
    25% { opacity: 1; transform: translateY(0px); }
    30% { opacity: 0; transform: translate3d(-35px, 35px, 0)}
    80% { opacity: 0; }
    100% { opacity: 0; }
}

/*Vertical Flip Animation*/

@keyframes flip {
    0% { opacity: 0; }
    5% { opacity: 0; transform: rotateX(180deg); }
    10% { opacity: 1; transform: translateY(0px); }
    25% { opacity: 0; transform: translateY(0px); }
    30% { opacity: 0; transform: translateY(0px); }
    80% { opacity: 0; }
    100% { opacity: 0; }
}
