a {
display:inherit;
color:inherit;
text-decoration: inherit;
}

.Container-bounds {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.Container-map-bounds { 
display: table-caption; 
flex-direction: row; 
flex-wrap: wrap; 
justify-content: center; 
}

.Tuber-map {
    display: flex;
    width: 320px;
    margin: 15px;
    padding: 5px;
}

.Container-map-bounds .Thanks {
  opacity: 0;
  cursor: pointer;
  transform: translateY(-0.5em);
  transition: 0.25s all ease-in-out;
}

.Container-map-bounds:hover .Thanks {
  opacity: 1;
  transform: translateY(0em);
}

.Tuber-logo { 
border-radius: 50%; 
width: 88px; 
height: 88px; 
}

.Tuber-name {
    padding-left: 20px;
}

.Thanks {
color:aqua;
text-align:center;
}