
.App-logo-outer { height: 40vmin;}
.App-logo { height: 40vmin; pointer-events: none;}
button {
all:initial;
text-decoration:none;
}



@media (prefers-reduced-motion: no-preference) {
    .App-logo { animation: App-logo-spin 1s ease-in-out infinite alternate; }
    .App-logo-outer { animation: App-logo-shift 2s infinite ease-in-out; }
}

@keyframes App-logo-spin {
    from { transform: rotate(-10deg); }

    to { transform: rotate(10deg); }
}

@keyframes App-logo-shift {
    0% { transform: translateX(-1.5em); }

    50% { transform: translateX(3em); }

    100% { transform: translateX(-1.5em); }
}
