.App {
scroll-snap-type: y mandatory;

}
.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.page {
    scroll-snap-align:center;
position:relative;
overflow-y:auto;
overscroll-behavior:contain;
height: 60vh;
text-align:center;
padding:20vh;
}